.hd-app *,
.hd-app *::before,
.hd-app *::after {
  box-sizing: border-box;
}
.hd-app body,
.hd-app h1,
.hd-app h2,
.hd-app h3,
.hd-app h4,
.hd-app h5,
.hd-app h6,
