}
.hd-app input,
.hd-app button,
.hd-app textarea,
.hd-app select {
  font: inherit;
}
.hd-app a,
.hd-app button {
  cursor: pointer;
}
.hd-app a[data-state=loading],
.hd-app button[data-state=loading] {
  cursor: wait;
}
.hd-app a[data-state=disabled],
.hd-app button[data-state=disabled] {
  cursor: not-allowed;
